'use strict';

import React from 'react';
import SiteStore from '../stores/SiteStore';
import classNames from 'classnames';

export default class Header extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.stateChanged = this.stateChanged.bind(this);
        this.state = {};
    }
    stateChanged() {
        this.setState({
            navbarOpaque: SiteStore.getScrollTop() > 70
        });
    }
    componentWillMount() {
        SiteStore.addChangeListener(this.stateChanged);
    }
    componentWillUnmount() {
        SiteStore.removeChangeListener(this.stateChanged);
    }
    render() {
        let navClasses = classNames({
            navbar: true,
            'navbar-custom': true,
            'navbar-fixed-top': true,
            'navbar-opaque': this.state.navbarOpaque === undefined ? false : this.state.navbarOpaque
        });
        return(
            <nav className={navClasses}>
                <div className="container">
                    <div className="navbar-header">
                        <button aria-controls="navbar" aria-expanded="false" className="navbar-toggle collapsed" data-target="#navbar" data-toggle="collapse" type="button">
                            <span className="sr-only">Toggle navigation</span>
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>
                            <span className="icon-bar"></span>
                        </button>
                        <a className="navbar-brand" href="#"><span className="bold orange">FFXIV</span>APP</a>
                    </div>
                    <div className="navbar-collapse collapse" id="navbar">
                        <ul className="nav navbar-nav navbar-right">
                            <li>
                                <a href="./">Fixed top
                                    <span className="sr-only">(current)</span>
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        );
    }
}



